import {TabBar} from "antd-mobile";
import {useLocation, useNavigate} from "react-router";
import {useEffect, useState} from "react";
import {Icon} from "../icon/icon";
import './index.scss'

const Bottom = () => {
    const navigate = useNavigate()
    const location = useLocation()
    const {pathname} = location

    //
    const [clientHeight, setClientHeight] = useState(null)
    //
    const [showHeight, setShowHeight] = useState(null)

    useEffect(() => {
        //, , clientHeight
        setClientHeight(document.documentElement.clientHeight)
        setShowHeight(document.documentElement.clientHeight)

        window.onresize = () => {
            //
            /*if (document.activeElement.tagName == "INPUT" || document.activeElement.tagName == "TEXTAREA") {
                window.setTimeout(function () {
                    document.activeElement.scrollIntoViewIfNeeded();
                }, 0);
            }*/

            return (() => {
                //, showHeight
                setShowHeight(document.body.clientHeight)
            })()
        }

        return () => {
            window.onresize = () => {
            }
        }
    }, [])

    const setRouteActive = (value) => {
        navigate(value)
    }

    const tabs = [
        {
            key: '/item-list',
            title: '',
            // icon: <AppOutline />,
            icon: <Icon name='shouye' />
        },
        {
            key: '/item-add',
            title: '',
            icon: <Icon name='jia'/>
        },
        /*{
            key: '/message',
            title: '',
            // icon: <MessageOutline />,
        },*/
        {
            key: '/me',
            title: '',
            // icon: <UserOutline />,
            icon: <Icon name='wode'/>
        },
    ]

    if (showHeight && clientHeight && showHeight < (clientHeight - 10)) {
        //  , 
        return <div></div>
    }

    return (
        <TabBar activeKey={pathname} onChange={value => setRouteActive(value)}>
            {tabs.map(item => (
                <TabBar.Item key={item.key} icon={item.icon} title={item.title}/>
            ))}
        </TabBar>
    )
}

export default Bottom